@media screen and (max-width: 800px) {
    .image-container {
        flex-direction: column;
    }

    .sun {
        width: 10vmin;
        position: absolute;
        transform: translate(0vmin, -15vmin);
    }

    .world {
        width: 9vmin;
        position: absolute;
        transform: translate(0vmin, -20vmin);
    }

    .image-scale {
        width: 100%;
    }

    .text-image-container {
        display: flex;
        flex-direction: column;
    }

    /* FID */
    .single-page-image {
        width: 100%;
    }

    .fid {
        width: 9vmin;
        position: absolute;
        transform: translate(0vmin, -20vmin);
    }
}

@media screen and (min-width: 800px) {
    .image-container {
        flex-direction: row;
    }

    .sun {
        width: 28vmin;
        position: absolute;
        transform: translate(100vmin, -28vmin);
    }

    .world {
        width: 30vmin;
        margin-right: auto;
    }

    .image-scale {
        width: 80vmin;
    }

    .text-image-container {
        display: flex;
        flex-direction: row;
        gap: 10vmin;
    }

    /* FID */
    .single-page-image {
        width: 80vmin;
        margin-left: auto;
        margin-right: auto;
    }

    .fid {
        width: 37vmin; 
        position: absolute;
        transform: translate(120vmin, -50vmin);      
    }
}

.CaseStudy {
    display: flex;
    flex-direction: column;
    gap: 16vmin;
}

.info-container {
    display: flex;
    flex-direction: row;
    gap: 30vmin;
}

.h1 {
    margin: 0;
}

.p {
    margin: 0;
}

.h3 {
    margin: 0;
}

.a {
    font-size: 2.2vmin;
    opacity: 0.5;
}

.caption {
    font-size: 1.9vmin;
    opacity: 0.6;
}

.section {
    display: flex;
    flex-direction: column;
    gap: 2vmin;

    position: relative;
}

.section-desc {
    display: flex;
    flex-direction: column;
    gap: 2vmin;    
}

.image-container {
    display: flex;
    gap: 10vmin;
}

.page-image {
    width: 100%;
    height: 100%;
}

.image-with-caption {
    display: flex;
    flex-direction: column;
    gap: 1vmin;
}

.center {
    margin-left: auto;
    margin-right: auto;
}

.last-section {
    padding-bottom: 0;
    margin-bottom: 0;
}

/* FID */
.single {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
}